import {
  GET_EMPLOYEES,
  GET_EMPLOYEES_SUCCESS,
  GET_EMPLOYEES_FAIL,
  CREATE_EMPLOYEE,
  CREATE_EMPLOYEE_SUCCESS,
  CREATE_EMPLOYEE_FAIL,
  UPDATE_EMPLOYEE,
  UPDATE_EMPLOYEE_SUCCESS,
  UPDATE_EMPLOYEE_FAIL,
  DELETE_EMPLOYEE,
  DELETE_EMPLOYEE_SUCCESS,
  DELETE_EMPLOYEE_FAIL,
  GET_EMPLOYEE_BY_ID,
  GET_EMPLOYEE_BY_ID_SUCCESS,
  GET_EMPLOYEE_BY_ID_FAIL,
  UPLOAD_EMPLOYEE_IMAGE,
  UPLOAD_EMPLOYEE_IMAGE_SUCCESS,
  UPLOAD_EMPLOYEE_IMAGE_FAIL,
  RESET_CURRENT_EMPLOYEE,
} from "../actions/EmployeeActions";

const initialState = {
  listEmployees: [], // Danh sch nhn vin
  loading: false, // Trng thi ti
  error: null, // Li
  currentEmployee: null, // Nhn vin hin ti
  totalElements: 0, // Tng s nhn vin
};

const employeeReducer = (state = initialState, action) => {
  switch (action.type) {
    // Cc action yu cu d liu (bt u ti)
    case GET_EMPLOYEES:
    case CREATE_EMPLOYEE:
    case UPDATE_EMPLOYEE:
    case DELETE_EMPLOYEE:
    case GET_EMPLOYEE_BY_ID:
    case UPLOAD_EMPLOYEE_IMAGE:
      return {
        ...state,
        loading: true,
        error: null,
      };

    // X l li t cc action
    case GET_EMPLOYEES_FAIL:
    case CREATE_EMPLOYEE_FAIL:
    case UPDATE_EMPLOYEE_FAIL:
    case DELETE_EMPLOYEE_FAIL:
    case GET_EMPLOYEE_BY_ID_FAIL:
    case UPLOAD_EMPLOYEE_IMAGE_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload, // Truyn li vo state
      };

    // Ly danh sch nhn vin thnh cng
    case GET_EMPLOYEES_SUCCESS:
      return {
        ...state,
        listEmployees: action.payload.data,
        totalElements: action.payload.totalElements, // Cp nht tng s nhn vin
        loading: false,
        error: null,
      };

    // To nhn vin thnh cng
    case CREATE_EMPLOYEE_SUCCESS:
      return {
        ...state,
        listEmployees: [...state.listEmployees, action.payload.data], // Thm nhn vin mi vo danh sch
        totalElements: state.totalElements + 1, // Tng s lng nhn vin
        loading: false,
        error: null,
      };

    // Cp nht nhn vin thnh cng
    case UPDATE_EMPLOYEE_SUCCESS:
      console.log("action.payload", action.payload);
      return {
        ...state,
        listEmployees: state.listEmployees.map((employee) =>
          employee.id === action.payload.data.id
            ? action.payload.data
            : employee
        ), // Cp nht nhn vin trong danh sch
        currentEmployee: action.payload, // Cp nht thng tin nhn vin hin ti
        loading: false,
        error: null,
      };

    // Xa nhn vin thnh cng
    case DELETE_EMPLOYEE_SUCCESS:
      return {
        ...state,
        listEmployees: state.listEmployees.filter(
          (employee) => employee.id !== action.payload
        ), // Loi b nhn vin khi danh sch
        totalElements: state.totalElements - 1, // Gim tng s nhn vin
        loading: false,
        error: null,
      };

    // Ly thng tin nhn vin theo ID thnh cng
    case GET_EMPLOYEE_BY_ID_SUCCESS:
      console.log("action.payload", action.payload);
      return {
        ...state,
        currentEmployee: action.payload, // Cp nht nhn vin hin ti
        loading: false,
        error: null,
      };

    // Upload nh nhn vin thnh cng
    case UPLOAD_EMPLOYEE_IMAGE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: null,
      };

    // Reset thng tin nhn vin hin ti sau khi ng dialog
    case RESET_CURRENT_EMPLOYEE:
      return {
        ...state,
        currentEmployee: null, // Hoc initialState cho currentEmployee ca bn
      };

    default:
      return state; // Tr v state mc nh nu khng c action ph hp
  }
};

export default employeeReducer;
